@use "global" as *;

.inner {
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    max-width: rem(600);

    @include mq("md") {
      max-width: $inner-pc + strip-unit($padding-pc) * 2;
      padding-right: $padding-pc;
      padding-left: $padding-pc;
      width: 100%;
    }
  }
